import React from "react";



function Accordion({item, open}){
    return(
        <div>
            <div className="title">
                <div>
                    {item.title}
                </div>
                <div onClick={() => open(item.id)} className="switcher"> 
                    {item.isOpen? "-" : "+"}
                </div> 
            </div>
            <div className={item.isOpen? 'open' : 'hidden'}>
                {item.text}
            </div>
        </div>
    )
};

export default Accordion;