import React, { useState } from "react";
import Accordion from "./Accordion";

function App(){

    const content = [
        {
            id: 1,
            isOpen: false,
            title: "Заголовок 1",
            text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. "
        },
        {
            id: 2,
            isOpen: false,
            title: "Заголовок 2",
            text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. "
        },
        {
            id: 3,
            isOpen: false,
            title: "Заголовок 3",
            text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. "
        },
        {
            id: 4,
            isOpen: false,
            title: "Заголовок 4",
            text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. "
        },
    ];

    const [items, setItems] = useState(content)

    const open = id => {
        setItems(items.map(item =>{
            if(id === item.id){
                return {
                    ...item,
                    isOpen: !item.isOpen
                }
            }
            return item;
        }))
    }

    let accordion = items.map(item => {
        return <Accordion item={item} open={open}/>
    });



    return(
        <div className="body">
            {accordion}
        </div>
    )
}

export default App;